import { useEffect, useState } from "react";
import classes from "./AvailableMeals.module.css";
import Card from "../UI/Card/Card";
import MealItem from "./MealItem/MealItem";

const AvailableMeals = () => {
  const [meals, setMeals] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [httpError, setHttpError] = useState("");
  useEffect(() => {
    const getMeals = async () => {
      setIsLoading(true);
      const responce = await fetch(
        "https://react-http-e86b1-default-rtdb.europe-west1.firebasedatabase.app/meals.json"
      );

      if (!responce.ok) {
        throw new Error("Failed to get data");
      }

      const responceData = await responce.json();

      const loadedMeals = [];
      for (var data in responceData) {
        loadedMeals.push({
          id: data,
          name: responceData[data].name,
          description: responceData[data].description,
          price: responceData[data].price
        });
      }

      setMeals(loadedMeals);
      setIsLoading(false);
    };

    getMeals().catch((error) => {
      setIsLoading(false);
      console.log(error);
      setHttpError(error.message);
    });
  }, []);

  const availableMeals = meals.map((meal) => (
    <MealItem
      id={meal.id}
      key={meal.id}
      name={meal.name}
      description={meal.description}
      price={meal.price}
    />
  ));

  let content = <span className={classes.spinner}></span>;
  if (!isLoading) {
    content = (
      <Card>
        <ul>{availableMeals}</ul>
      </Card>
    );
  }

  if (httpError) {
    content = <p className={classes.mealError}>{httpError}</p>;
  }

  return <section className={classes.meals}>{content}</section>;
};

export default AvailableMeals;
