import React, { useState } from "react";
import Cart from "./components/Cart/Cart";
import Header from "./components/Layout/Header";
import Meals from "./components/Meals/Meals";
import CartProvider from "./store/CartProvider";
function App() {
  const [isCartShawn, setCartShawn] = useState(false);

  const onOpenCart = () => {
    setCartShawn(true);
  };
  const onCloseCart = () => {
    setCartShawn(false);
  };

  return (
    <CartProvider>
      {isCartShawn && <Cart onClose={onCloseCart} />}
      <Header onCartOpen={onOpenCart} />
      <main>
        <Meals />
      </main>
    </CartProvider>
  );
}

export default App;
