import React, { useContext, useState } from "react";
import CartContext from "../../store/cart-context";
import CartItem from "./CartItem";
import Modal from "../UI/Modal/Modal";
import classes from "./Cart.module.css";
import Checkout from "./Checkout";

const Cart = (props) => {
  const [isCheckOut, setIsCheckOut] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [didSubmit, setDidSubmit] = useState(false);
  const cartCtx = useContext(CartContext);
  const totalAmount = `$${cartCtx.totalAmount.toFixed(2)}`;
  const addItemHandler = (item) => {
    cartCtx.addItem({ ...item, amount: 1 });
  };
  const removeItemHandler = (id) => {
    console.log(id);
    cartCtx.removeItem(id);
  };
  const cartItems = (
    <ul className={classes["cart-items"]}>
      {cartCtx.items.map((item) => (
        <CartItem
          key={item.id}
          name={item.name}
          price={item.price}
          amount={item.amount}
          onAdd={addItemHandler.bind(null, item)}
          onRemove={removeItemHandler.bind(null, item.id)}
        />
      ))}
    </ul>
  );

  const openCheckOutHandler = () => {
    setIsCheckOut(true);
  };
  const cartActions = (
    <div className={classes.actions}>
      <button className={classes["button--alt"]} onClick={props.onClose}>
        Close
      </button>
      <button className={classes.button} onClick={openCheckOutHandler}>
        Order
      </button>
    </div>
  );

  const confirmFormHandler = async (userData) => {
    setIsSubmitting(true);
    await fetch(
      "https://react-http-e86b1-default-rtdb.europe-west1.firebasedatabase.app/orders.json",
      {
        method: "POST",
        body: JSON.stringify({ user: userData, orderedItems: cartCtx.items })
      }
    );

    setIsSubmitting(false);
    setDidSubmit(true);
  };

  const modalData = (
    <React.Fragment>
      {cartItems}
      <div className={classes.total}>
        <span>Total amount</span>
        <span>{totalAmount}</span>
      </div>
      {isCheckOut && (
        <Checkout onCancel={props.onClose} onConfirm={confirmFormHandler} />
      )}
      {!isCheckOut && cartActions}
    </React.Fragment>
  );
  const submittingData = <p>Məlumatlar bazaya göndərilir..</p>;
  const submittedData = <p>Məlumatlar bazaya göndərildi!</p>;
  return (
    <Modal onClick={props.onClose}>
      {!isSubmitting && !didSubmit && modalData}
      {isSubmitting && submittingData}
      {!isSubmitting && didSubmit && submittedData}
    </Modal>
  );
};

export default Cart;
